<template>
<u-linear-layout direction="vertical">
    <u-cascade-select converter="last-value" v-model="value" :categories="[
        { label: '' },
        { label: '' },
        { label: '' },
    ]" :data="data">
    </u-cascade-select>
    <div>{{ value }}</div>
</u-linear-layout>
</template>
<script>
export default {
    data() {
        return {
            value: '213',
            data: [
                { text: '', value: '1', children: [
                    { text: '', value: '11', children: [
                        { text: '', value: '111' },
                        { text: '', value: '112' },
                        { text: '', value: '113' },
                    ] },
                    { text: '', value: '12', children: [
                        { text: '', value: '121' },
                        { text: '', value: '122' },
                        { text: '', value: '123' },
                    ] },
                    { text: '', value: '13' },
                ] },
                { text: '', value: '2', children: [
                    { text: '', value: '21', children: [
                        { text: '', value: '211' },
                        { text: '', value: '212', disabled: true },
                        { text: '', value: '213' },
                    ] },
                    { text: '', value: '22', disabled: true },
                    { text: '', value: '23', children: [
                        { text: '', value: '231' },
                        { text: '', value: '232' },
                        { text: '', value: '233', disabled: true },
                        { text: '', value: '234', disabled: true },
                    ] },
                ] },
            ],
        };
    },
};
</script>



