<template>
<div>
    <u-checkbox v-model="allChecked" @check="checkAll($event.value)"></u-checkbox>
    <u-checkboxes v-model="checkedList" @check="onCheck($event.value)">
        <u-checkbox label=""></u-checkbox>
        <u-checkbox label=""></u-checkbox>
        <u-checkbox label=""></u-checkbox>
        <u-checkbox label=""></u-checkbox>
    </u-checkboxes>
    {{ checkedList }}
</div>
</template>
<script>
export default {
    data() {
        return {
            allChecked: false,
            checkedList: [],
        };
    },
    methods: {
        checkAll(checked) {
            this.checkedList = checked ? ['', '', '', ''] : [];
        },
        onCheck(value) {
            if (this.checkedList.length === 4)
                this.allChecked = true;
            else if (this.checkedList.length === 0)
                this.allChecked = false;
            else
                this.allChecked = null;
        },
    },
};
</script>



