<template>
<u-linear-layout direction="vertical">
    <u-cascade-select converter="join" v-model="value" :categories="[
        { label: '' },
        { label: '' },
        { label: '' },
    ]" :data="data">
    </u-cascade-select>
    <div>{{ value }}</div>
</u-linear-layout>
</template>
<script>
export default {
    data() {
        return {
            value: ',,',
            data: [
                { text: '', value: '', children: [
                    { text: '', value: '', children: [
                        { text: '', value: '' },
                        { text: '', value: '' },
                        { text: '', value: '' },
                    ] },
                    { text: '', value: '', children: [
                        { text: '', value: '' },
                        { text: '', value: '' },
                        { text: '', value: '' },
                    ] },
                    { text: '', value: '' },
                ] },
                { text: '', value: '', children: [
                    { text: '', value: '', children: [
                        { text: '', value: '' },
                        { text: '', value: '', disabled: true },
                        { text: '', value: '' },
                    ] },
                    { text: '', value: '', disabled: true },
                    { text: '', value: '', children: [
                        { text: '', value: '' },
                        { text: '', value: '' },
                        { text: '', value: '', disabled: true },
                        { text: '', value: '', disabled: true },
                    ] },
                ] },
            ],
        };
    },
};
</script>



